  font-weight: bold; }

/* Tables
   ========================================================================== */
/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0; }

td, th {
  padding: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

html {
  font-family: Font-Regular;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased; }

body {
  background-color: #fff;
  color: #666;
  line-height: 20px; }

a {
  color: #666;
  text-decoration: none; }
  a:hover {
    color: #000;
    text-decoration: underline; }
  a:active {
    color: #000; }

h1, h2, h3, h4, h5, h6 {
  line-height: normal; }

p {
  line-height: normal; }

b, strong, i, em {
  font-style: normal;
  font-weight: normal; }

b, strong {
  font-family: Font-Bold; }

i, em {
  font-family: Font-Italic; }

small {
  font-size: 100%; }

mark {
  background-color: transparent; }

img {
  vertical-align: middle; }

table {
  background-color: transparent;
  max-width: 100%; }

th {
  text-align: left; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

.clearfix:after {
  content: "";
  display: table;
  clear: both; }

.hidden {
  display: none !important;
  visibility: hidden; }

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.visuallyhidden.focusable:active, .visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto; }

.invisible {
  visibility: hidden; }


